<template>
  <div id="app">
    <div class="placeholder"></div>
    <Headers :HeaderData="HeaderData"></Headers>
    <div class="container">
      <div class="margin-top-15">
        <div class="float-left index-list">
          <Articles :ArticleData="ArticleData"></Articles>
          <Message :submit="submit"/>
        </div>
        <div class="float-right tuijian">
          <Host :HostData="HostData"></Host>
          <HostArticle :HostArticleData="HostArticleData"></HostArticle>
          <Total :TotalData="TotalData"></Total>
          <Links :LinksData="LinksData"></Links>
        </div>
      </div>
    </div>
    <Footers :FootersData="FootersData"></Footers>
    <Top></Top>
  </div>
</template>
<script>
//组件引入
import Headers from 'components/header.vue';
import Articles from 'components/Article.vue';
import Message from 'components/Message.vue';
import Host from 'components/host.vue';
import HostArticle from 'components/hostArticle.vue';
import Total from 'components/total.vue';
import Links from 'components/links.vue';
import Footers from 'components/footer.vue';

//数据来源
import HeaderData from 'common/dataControl/HeaderJson';
import ArticleData from 'common/dataControl/ArticleJson';
import HostData from 'common/dataControl/HostJson';
import HostArticleData from 'common/dataControl/HostArticleJson';
import TotalData from 'common/dataControl/TotalJson';
import LinksData from 'common/dataControl/LinksJson';
import FootersData from 'common/dataControl/FootersJson';

function submit(){
  let data = {
    id:ArticleData.id,
    userName:$('#inpName').val(),
    userEmail:$('#inpEmail').val(),
    userAsk:$('#txaArticle').val()
  };
  if(data.userName.length != 0 && data.userEmail.length != 0 && data.userAsk.length != 0){
    $.post('http://www.linzantian.com/index/article/message',data,function(r){
      if(r.res=='0000'){
        layer.msg(r.msg);
      }else{
        layer.msg(r.msg);
      }           
    })
  }else {
    layer.msg('请填写相关内容');
  }
};

export default {
  name: 'app',
  data(){
    return{
      HeaderData,
      ArticleData,
      HostData,
      HostArticleData,
      TotalData,
      LinksData,
      FootersData,
      submit
    }
  },
  components:{
    Headers,
    Articles,
    Message,
    Host,
    HostArticle,
    Total,
    Links,
    Footers,
    Top:resolve=>require(['components/top.vue'],resolve)
  },
  mounted:function(){
  }
}
</script>
<style scoped>
@import "../../common/css/common.css";
.index-list{
  width:74.5%;
}
.tuijian{
  width:24.3%;
}
</style>


